export default (editor, opts = {}) => {
  const c = opts.summernote

  editor.setCustomRte({
    enable: async(el, rte) => {
      // detect summernote
      const doc = el.ownerDocument
      const win = doc.defaultView || doc.parentWindow
      const jq  = win.jQuery
      if (!jq.fn.summernote) {
        console.error('Summernote script is required.')
        return
      }
      rte = jq(el).find('.note-editor')

      if (rte.length > 0) {
        rte.summernote('enable')
      } else {
        rte = jq(el).html('<div class="note-editor" contenteditable="true"></div>').find('.note-editor').summernote(c)
      }

      return rte;
    },

    disable(el, rte) {
      const doc = el.ownerDocument
      const win = doc.defaultView || doc.parentWindow
      const jq  = win.jQuery

      jq(el).find('.note-editor').remove()
    }
  })

  editor.on('load', function () {
    opts.addResource(
      'https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.7/umd/popper.min.js'
    )
    opts.addResource(
      'https://stackpath.bootstrapcdn.com/bootstrap/4.1.3/js/bootstrap.min.js'
    )
    opts.addResource(
      c.css || 'https://cdnjs.cloudflare.com/ajax/libs/summernote/0.8.11/summernote.css',
      'link'
    )
    opts.addResource(
      c.js || 'https://cdnjs.cloudflare.com/ajax/libs/summernote/0.8.11/summernote.min.js'
    )
  })
}
